import { CModal, CModalHeader, CModalBody, CModalTitle, CButton, CForm, CCallout } from '@coreui/react'
import { useState } from 'react'
import { axiosPrivate } from '../../api/axios'
import Spinner from '../../Components/spinner'
import BaseForm from '../baseForm'
export const ModalAggiungiFornitore = (props) => {
    const [validated, setValidated] = useState(false)
    const [isLoading, setIsLoading] = useState(false)
    const [success, setSuccess] = useState({success: false, message: ""})
    const [fornitoreTemp, setFornitoreTemp] = useState({
          fornitore_id: "",
          cod_fornitore: "",
          ragione_sociale: "",
          sito_legale: "",
          sito_operativo: "",
          vat_number: "",
          codice_fiscale: "",
          email: "",
          pec: "",
          unic_code: "",
          tel: "",
          referente: ""
      })

    const handleSubmit = async (e) =>{
        e.preventDefault()
        const form = e.currentTarget
        if (form.checkValidity() === false) {
        }
        setValidated(true)
        setIsLoading(true)
        try{
            const res = await axiosPrivate.post("/fornitore", fornitoreTemp)
            setSuccess({success: true, message: res.data.message})
            setIsLoading(false)
        }
        catch (err){
            console.log(err)
            setIsLoading(false)
        }
    }

    const formData = [
        [
          {
            required: true,
            id: "ragione_sociale",
            label: "Ragione Sociale",
            type : "input",
            area_territoriale_id: "",
            cliente_id: ""
          },
          {
            required: true,
            id: "cod_fornitore",
            label: "Codice Fornitore",
            type : "input",
            placeholder: ""
          },
          {}
        ],
        [
          {
            required: false,
            id: "sito_legale",
            label: "Sede legale",
            type : "input",
            placeholder: ""
          },
          {
            required: false,
            id: "sito_operativo",
            label: "Sede operativa",
            type : "input",
            placeholder: ""
          },
          {
            required: false,
            id: "vat_number",
            label: "P. IVA",
            type : "input",
            placeholder: ""
          },
        ],
        [
          {
            required: false,
            id: "codice_fiscale",
            label: "Codice fiscale",
            type : "input",
            placeholder: ""
          },
          {
            required: false,
            id: "email",
            label: "E-mail",
            type : "input",
            placeholder: ""
          },
          {
            required: false,
            id: "pec",
            label: "Pec",
            type : "input",
            placeholder: ""
          },
        ],
        [
          {
            required: false,
            id: "unic_code",
            label: "Codice univoco",
            type : "input",
            placeholder: ""
          },
          {
            required: false,
            id: "tel",
            label: "Telefono",
            type : "input",
            placeholder: ""
          },
          {
            required: false,
            id: "referente",
            label: "Referente",
            type : "input",
            placeholder: ""
          },
        ],
    ]

    return (
        <>
            { isLoading && <Spinner/>}
            <CModal size="xl" visible={props.isVisible} onClose={() => props.onClose()}>
            <CModalHeader style={{backgroundColor: "#E5F4FF"}}>
                <CModalTitle>{props.title}</CModalTitle>
            </CModalHeader>
            <CModalBody>
                {success.success &&  <CCallout className="py-2" color="success">
                    <h1 className="size-header m-0">Successo</h1>
                    <p className="size-text m-0">{success.message}</p>
                </CCallout>}
                <CForm onSubmit={handleSubmit} noValidate validated={validated}>
                    <BaseForm state={fornitoreTemp} setStateFunction={setFornitoreTemp} data_set={formData}/>
                    <CButton type='submit' shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm my-2">Crea</CButton> 
                </CForm>
            </CModalBody>
            </CModal>
        </>
    )
}