import { CModal, CModalHeader, CModalBody, CModalTitle, CButton, CForm, CCallout } from '@coreui/react'
import { useState } from 'react'
import BaseForm from '../baseForm'
export const ModalAggiungiPl = (props) => {
    const [validated, setValidated] = useState(false)
    const [success, setSuccess] = useState(false)
    const [pl, setPl] = useState({
        pl: "",
        indirizzo: "",
        impianto: "",
        targhetta: "",
        latitudine: "",
        longhitudine: "",
        tipologia_palo: "",
        materiale: "",
        anzianita: "",
        altezza: "",
        etichetta: "",
        flag: "",
        note: ""
    })

    const handleSubmit = (e) =>{
        e.preventDefault()
        const form = e.currentTarget
        if (form.checkValidity() === false) {
        }
        setValidated(true)
        console.log(pl)
    }

    const formData = [
        [
            {
                required: false,
                id: "pl",
                label: "Punto Luce",
                type : "input",
                placeholder: ""
            },
            {
                required: false,
                id: "indirizzo",
                label: "Indirizzo",
                type : "input",
                placeholder: ""
            },
            {
                required: false,
                id: "inpianto",
                label: "Inpianto",
                type : "input",
                placeholder: ""
            }
        ],
        [
            {
                required: false,
                id: "targhetta",
                label: "Targhetta",
                type : "input",
                placeholder: ""
            },
            {
                required: false,
                id: "latitudine",
                label : "Latitudine",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "longitudine",
                label : "Longitudine",
                type : "input",
                placeholder : ""
            }
        ],
        [
            {
                required: false,
                id: "tipologia_palo",
                label: "Tipologia palo",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "materiale",
                label: "Materiale",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "anzianita",
                label: "Anzianit",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "altezza",
                label: "Altezza",
                type : "input",
                placeholder : ""
            }
        ],
        [
            {
                required: false,
                id: "etichetta",
                label: "Etichetta",
                type : "select",
                options: ["Nessuna"]
            },
            {
                required: false,
                id: "flag",
                label : "Flag",
                type : "input",
                placeholder : ""
            }
        ],
        [
            {
                required: false,
                id: "note",
                label: "Note",
                type : "input",
                placeholder: ""
            },
        ]
    ]
    return (
            <CModal size="xl" visible={props.isVisible} onClose={() => props.onClose()}>
            <CModalHeader style={{backgroundColor: "#E5F4FF"}}>
                <CModalTitle>{props.title}</CModalTitle>
            </CModalHeader>
            <CModalBody>
                {success.success &&  <CCallout className="py-2" color="success">
                    <h1 className="size-header m-0">Successo</h1>
                    <p className="size-text m-0">{success.message}</p>
                </CCallout>}
                <CForm onSubmit={handleSubmit}
                noValidate
                validated={validated}>
                    <BaseForm state={pl} setStateFunction={setPl} data_set={formData}  />
                    <CButton type='submit' shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm my-2">Inserisci PL</CButton> 
                </CForm>
            </CModalBody>
            </CModal>
    )
}