import React from 'react'
import { CRow, CCol, CFormSelect } from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilPenAlt } from '@coreui/icons'
import { useOutletContext } from 'react-router-dom'

export const VerStrutturale = (props) => {
  const [plData, setPlData, setIsVisibleAggiungiVoce, setIsVisibleSezioneTesta] = useOutletContext()

  return (
      <CRow>
        <CCol>
            <div className='size-text color-base weight-600 mb-2'>Scegli Modello</div>
            <CFormSelect
            className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
            onChange={e => setPlData({...plData, verifica_strutturale : {...plData.verifica_strutturale, modello: e.target.value}})}
            >
              <option className='size-text color-base'>Torri 2 Tronchi</option>
            </CFormSelect>
            <div className='size-text color-base weight-700 my-2' onClick={() => setIsVisibleSezioneTesta(true)}>SEZIONE DI TESTA <CIcon size='sm' icon={cilPenAlt}/></div>
            <div className='size-text color-base weight-700 mb-2'>SEZIONE SECONDO INNESTO <CIcon size='sm' icon={cilPenAlt}/></div>
            <div className='size-text color-base weight-700 mb-2'>SEZIONE PRIMO INNESTO <CIcon size='sm' icon={cilPenAlt}/></div>
            <div className='size-text color-base weight-700 mb-2'>SEZIONE BASE <CIcon size='sm' icon={cilPenAlt}/></div>
        </CCol>
        <CCol>
            <div className='size-text color-base weight-600 mb-2'>Anteprima Disegno Grafico</div>
            img
        </CCol>
      </CRow>
  )
}
