import { CModal, CModalHeader, CModalBody, CModalTitle, CFormInput, CFormCheck, CButton } from '@coreui/react'
import { useState } from 'react'
import CIcon from '@coreui/icons-react'
import { cilPlus } from '@coreui/icons'
export const ModalAggiungiVoce = (props) => {
    const [tempVoce, setTempVoce] = useState("")

    return (
            <CModal size="xl" visible={props.isVisible} onClose={() => props.onClose()}>
            <CModalHeader style={{backgroundColor: "#E5F4FF"}}>
                <CModalTitle>{props.title}</CModalTitle>
            </CModalHeader>
            <CModalBody>
                <div className='d-flex'>
                    <CFormInput
                    placeholder="Aggiungi voce"
                    className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                    onChange={e => setTempVoce(e.target.value)}
                    />
                    <CButton onClick={() => props.setVoci([...props.voci, tempVoce])} shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm mx-2 weight-600 px-2"><CIcon size='sm' icon={cilPlus}/></CButton>
                </div>
                <div style={{maxHeight: "75vh", overflowY: "scroll"}} className='border border-light px-2 my-2 rounded-1'>
                    {props.voci.map((voce, index) => 
                        <div key={index} className='size-text color-base my-2'><CFormCheck checked={props.plData.voci.includes(voce)} value={voce} onChange={e => e.target.checked ? props.setPlData({...props.plData, voci : [...props.plData.voci, e.target.value]}) : props.setPlData({...props.plData, voci : props.plData.voci.filter(voce => voce !== e.target.value)})}/>&nbsp;&nbsp;{voce}</div>
                        )}
                </div>
            </CModalBody>
            </CModal>
    )
}
