import React, { useEffect } from 'react'
import { useState } from 'react'
import CIcon from '@coreui/icons-react'
import { cilVerticalAlignCenter } from '@coreui/icons'
import { CHeaderBrand, CContainer, CCol, CForm, CCard, CCardBody, CNavbar } from '@coreui/react'
import { useParams, NavLink, Outlet } from 'react-router-dom'
import { ModalSezioneTesta } from '../../../Forms/puntoLuce/modal_sezione_testa'
import { ModalAggiungiVoce } from '../../../Forms/puntoLuce/modal_aggiungi_voce'
export const PuntoLuce = (props) => {
    const { id_pl} = useParams()
    const [isVisibleSezioneTesta, setIsVisibleSezioneTesta] = useState(false)
    const [isVisibleAggiungiVoce, setIsVisibleAggiungiVoce] = useState(false)
    const menuItems = [
        {
        text: "Dati Generali",
        to: ''
      },
        {
        text: "Visual Test",
        to: 'visual_testing'
      },
        {
        text: "Ultrasonic Test",
        to: 'ultrasonic_testing'
      },
        {
        text: "Magnetic Test",
        to: 'magnetic_testing'
      },
        {
        text: "Ver. Strutturale",
        to: 'ver_strutturale'
      },
        {
        text: "Ver. Elettromeccanica",
        to: 'ver_elettromeccanica'
      },
        {
        text: "Note",
        to: 'note'
      },
        {
        text: "Ver. Rettilineit",
        to: 'ver_rettilineita'
      },
        {
        text: "Attestato",
        to: 'attestato'
      }
      ]
    const [voci, setVoci] = useState([
        "Controllo del corretto aggancio dellelemento di sicurezza",
        "Controllo del funzionamento della serratura della portella", 
        "Controllo del fissaggio dei cavi in acciaio e che questi non siano attorcigliati allinterno della torre"
    ])
    const [plData, setPlData] = useState({
        dati_generali : {
            committente: "",
            costruttore: "",
            data: "",
            tbd: "",
            commessa: id_pl,
            tipoligia: "",
            n_torre: "",
            tipoligia_tirafondi: "",
            dist_asse_tirafondi: "",
            n_tirafondi: "",
            diametro_base: "",
            altezza: "",
            materiale: "",
            cond_superficiali: "",
            planimetria: "",
            indirizzo: ""
        },
        visual_testing : {
            esame: "",
            tipi_esame: [],
            dist_osservazione: "",
            attrezz_ausiliarie: "",
            angolo_osservazione: "",
            illuminazione: "",
        },
        ultrasonic_testing : {
            spessimetro_marca_modello: "",
            spessimetro_attrezz_ausiliarie: "",
            trasduttore_f_d_a: "",
            trasduttore_marca_tipo: "",
            rivelatore_universale_marca_tipo : "",
            rivelatore_universale_attrezz_ausiliarie: ""
        },
        magnetic_testing : {
            marca_modello: "",
            tipologia_corrente: "",
            attrezz_ausiliarie: "",
            f_d_a: "",
        },
        verifica_strutturale : {
            modello: "",
            sezione_testa: {},
            sezione_primo_innesto: {},
            sezione_secondo_innesto: {},
            sezione_base: {},
            img: ""
        },
        voci: [],
        note: [],
        verifica_rettilineita : {
            diametro : {
                tirafondo_1: "",
                tirafondo_2: "",
                n: "",
            },
            zona_misura : {
                0: "",
                90: "",
                180: "",
                360: ""
            }
        }
    })

    useEffect(() =>{
        console.log(plData)
    }, [plData])

    return (
    <>
    <ModalSezioneTesta title="Modifica Dati Sezione di Testa" isVisible={isVisibleSezioneTesta} plData={plData} onClose={() => setIsVisibleSezioneTesta(false)} setPlData={setPlData} />
    <ModalAggiungiVoce title="Aggiungi" isVisible={isVisibleAggiungiVoce} voci={voci} onClose={() => setIsVisibleAggiungiVoce(false)} setVoci={setVoci} plData={plData} setPlData={setPlData} />
    <CContainer className="my-3 contrast justify-content-center rounded-1">
        <CNavbar className="contrast rounded-1 w-100 d-flex justify-content-start rounded-1 m-0" expand='lg' colorScheme='light'>
                {menuItems.map((item, index) => 
                (
                    <NavLink key={index} className={({isActive, isPending}) => `${(isActive)? "color-highlight" : "color-base"} size-text weight-600 d-flex align-items-center p-2 mx-1 nav-link`} to={item.to} end>
                        <CIcon icon={item.icon} />&nbsp; {item.text}
                    </NavLink>
                ))}
        </CNavbar>
    </CContainer>
    <CContainer className="my-3 p-4 contrast justify-content-center rounded-1">
        <CCol className='d-flex justify-content-start mb-3'>
            <CHeaderBrand className='size-header weight-600 d-flex align-items-center'><CIcon size='lg' className='color-highlight' icon={cilVerticalAlignCenter}/>&nbsp;{id_pl}</CHeaderBrand>
        </CCol>
        <CCard className='background-light border-0 shadow-sm'>
            <CCardBody>
            <CForm className="justify-content-center rounded-1 px-3 py-2">
                <Outlet context={[plData, setPlData, setIsVisibleAggiungiVoce, setIsVisibleSezioneTesta]}/>
            </CForm>
            </CCardBody>
        </CCard>
    </CContainer>
    </>
    )
}
