import React from 'react'
import { CRow, CCol, CFormInput, CInputGroup, CInputGroupText } from '@coreui/react'
import { useOutletContext } from 'react-router-dom'

export const VerRettilineita = () => {
    const [plData, setPlData] = useOutletContext()
    return (
            <CRow>
            <CCol>
                <div className='size-subHeader color-base weight-700'>Diametro (mm)</div>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>Tirafondo n.1</CInputGroupText>
                    <CFormInput
                    onChange={e => setPlData({...plData, verifica_rettilineita: {...plData.verifica_rettilineita, diametro : {...plData.verifica_rettilineita.diametro, tirafondo_1 : e.target.value}}})}
                    placeholder="24"
                    className='size-text color-base weight-400 py-1 border-light'
                    />
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>Tirafondo n.2</CInputGroupText>
                    <CFormInput
                        onChange={e => setPlData({...plData, verifica_rettilineita: {...plData.verifica_rettilineita, diametro : {...plData.verifica_rettilineita.diametro, tirafondo_2 : e.target.value}}})}
                        placeholder=""
                        className='size-text color-base weight-400 py-1 border-light'
                        />
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>n</CInputGroupText>
                    <CFormInput
                        onChange={e => setPlData({...plData, verifica_rettilineita: {...plData.verifica_rettilineita, diametro : {...plData.verifica_rettilineita.diametro, n : e.target.value}}})}
                        placeholder=""
                        className='size-text color-base weight-400 py-1 border-light'
                        />
                </CInputGroup>
            </CCol>
            <CCol>
                <div className='size-subHeader color-base weight-700'>Zona di misura</div>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>0</CInputGroupText>
                    <CFormInput
                        onChange={e => setPlData({...plData, verifica_rettilineita: {...plData.verifica_rettilineita, zona_misura : {...plData.verifica_rettilineita.zona_misura, 0 : e.target.value}}})}
                        placeholder="24"
                        className='size-text color-base weight-400 py-1 border-light'
                        />
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>90</CInputGroupText>
                    <CFormInput
                        onChange={e => setPlData({...plData, verifica_rettilineita: {...plData.verifica_rettilineita, zona_misura : {...plData.verifica_rettilineita.zona_misura, 90 : e.target.value}}})}
                        placeholder=""
                        className='size-text color-base weight-400 py-1 border-light'
                        />
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>180</CInputGroupText>
                    <CFormInput
                        onChange={e => setPlData({...plData, verifica_rettilineita: {...plData.verifica_rettilineita, zona_misura : {...plData.verifica_rettilineita.zona_misura, 180 : e.target.value}}})}
                        placeholder=""
                        className='size-text color-base weight-400 py-1 border-light'
                        />
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>360</CInputGroupText>
                    <CFormInput
                        onChange={e => setPlData({...plData, verifica_rettilineita: {...plData.verifica_rettilineita, zona_misura : {...plData.verifica_rettilineita.zona_misura, 360 : e.target.value}}})}
                        placeholder=""
                        className='size-text color-base weight-400 py-1 border-light'
                        />
                </CInputGroup>
            </CCol>
        </CRow>
    )
}
