import React from 'react'
import { useOutletContext } from 'react-router-dom'
import BaseForm from '../../../Forms/baseForm'
export const MagneticTesting = () => {
    const [plData, setPlData] = useOutletContext()
    const formData = [
                [
                    {
                        required: false,
                        id: "marca_modello",
                        label: "Marca/Modello",
                        type : "select",
                        options : ["GILARDONI"]
                    },
                    {
                        required: false,
                        id: "tipologia_corrente",
                        label: "Tipologia/Corrente",
                        type : "select",
                        options : [" GILARDONI/TG DOPPIA"]
                    }
                ],
                [
                    {
                        required: false,
                        id: "attrezz_ausiliarie",
                        label: "Attrezzature Ausiliarie",
                        type : "input",
                        placeholder : "Blocco di Calibrazione a Gradini"
                    },
                    {
                        required: false,
                        id: "f_d_a",
                        label : "Frequenza/Dimensione/Angolo",
                        type : "input",
                        placeholder : "4 MHz/10 mm/0"
                    }
                ]
            ]

    return (
        <BaseForm state={plData} setStateFunction={setPlData} data_set={formData}/>
    )
}
