import React from 'react'
import { useOutletContext } from 'react-router-dom'
import BaseForm from '../../../Forms/baseForm'

export const VisualTesting = () => {
    const [plData, setPlData] = useOutletContext()
    const formData = [
        [
            {
                required: false,
                id: "esame",
                label: "Esame Diretto",
                type : "check"
            },
            {
                required: false,
                id: "dist_osservazione",
                label: "Distanza di osservazione",
                type : "select",
                options : ["<600 mm"]
            },
            {
                required: false,
                id: "attrezz_ausiliarie",
                label: "Attrezzature ausiliarie",
                type : "input",
                placeholder : "Calibro Millesimale"
            }
        ],
        [
            {
                required: false,
                id: "tipi_esame",
                label: "",
                type : "multi-check",
                checks : ["Non Assistito", "Assistito con Lente", "Assistito con specchio", "Assistito con endoscopio"]
            }
        ],
        [
            {
                required: false,
                id: "esame",
                label: "Esame Indiretto",
                type : "check"
            },
            {
                required: false,
                id: "angolo_osservazione",
                label : "Angolo di osservazione",
                type : "select",
                options : ["<600 mm"]
            },
            {
                required: false,
                id: "illuminazione",
                label: "Illuminazione",
                type : "input",
                placeholder : "Calibro Millesimale"
            }
        ]
    ]

    return (
        <BaseForm state={plData} setStateFunction={setPlData} data_set={formData}/>
    )
}
