import { CModal, CModalHeader, CModalBody, CModalTitle, CRow, CCol, CInputGroup, CInputGroupText, CFormInput, CFormSelect } from '@coreui/react'
export const ModalSezioneTesta = (props) => {

    const onClose = () =>{
        props.onClose()
    }

    return (
            <CModal size="xl" visible={props.isVisible} onClose={() => onClose()}>
            <CModalHeader style={{backgroundColor: "#E5F4FF"}}>
                <CModalTitle>{props.title}</CModalTitle>
            </CModalHeader>
            <CModalBody>
                <div className='size-text color-base weight-700 mb-2'>TRONCO E FLANGIA DI TESTA</div>
                <div className='size-text color-base weight-600 mb-2'>Orientamento/SP(mm)</div>
                <CRow className='w-75'>
                    <CCol>
                        <CInputGroup className='rounded-1 w-75 my-2'>
                            <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>N</CInputGroupText>
                            <CFormInput
                                placeholder="3,5"
                                className='size-text color-base weight-400 py-1 border-light'
                                />
                        </CInputGroup>
                    </CCol>
                    <CCol>
                        <CInputGroup className='rounded-1 w-75 my-2'>
                            <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>E</CInputGroupText>
                            <CFormInput
                                placeholder="3,4"
                                className='size-text color-base weight-400 py-1 border-light'
                                />
                        </CInputGroup>
                    </CCol>
                    <CCol>
                        <CInputGroup className='rounded-1 w-75 my-2'>
                            <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>S</CInputGroupText>
                            <CFormInput
                                placeholder="3,5"
                                className='size-text color-base weight-400 py-1 border-light'
                                />
                        </CInputGroup>
                    </CCol>
                    <CCol>
                        <CInputGroup className='rounded-1 w-75 my-2'>
                            <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>W</CInputGroupText>
                            <CFormInput
                                placeholder="3,5"
                                className='size-text color-base weight-400 py-1 border-light'
                                />
                        </CInputGroup>
                    </CCol>
                </CRow>
                <div className='size-text color-base weight-600 mb-2'>Saldature Longitudinali e Circonferenziali</div> 
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>VT</CInputGroupText>
                    <CFormSelect className='size-text color-base weight-400 py-1 border-light'>
                        <option>Corrosione</option>
                    </CFormSelect>
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>UT</CInputGroupText>
                    <CFormSelect className='size-text color-base weight-400 py-1 border-light'>
                        <option></option>
                    </CFormSelect>
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>MT</CInputGroupText>
                    <CFormSelect className='size-text color-base weight-400 py-1 border-light'>
                        <option>Corrosione</option>
                    </CFormSelect>
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>CRF</CInputGroupText>
                    <CFormInput
                        placeholder="85 cm"
                        className='size-text color-base weight-400 py-1 border-light'
                        />
                </CInputGroup>
                <CInputGroup className='rounded-1 w-75 my-2'>
                    <CInputGroupText className='size-text color-base weight-400 py-1 border-light'>Note</CInputGroupText>
                    <CFormInput
                        placeholder=""
                        className='size-text color-base weight-400 py-1 border-light'
                        />
                </CInputGroup>
                <div className='size-text color-base weight-600 mb-2'>Esito</div>
                <CFormInput
                        placeholder=""
                        className='size-text color-base weight-400 py-1 border-light w-50'
                        />
            </CModalBody>
            </CModal>
    )
}
