import { CModal, CModalHeader, CModalBody, CModalTitle, CButton, CForm, CCallout } from '@coreui/react'
import { useState } from 'react'
import { axiosPrivate } from '../../api/axios'
import Spinner from '../../Components/spinner'
import BaseForm from '../baseForm'
export const ModalAggiungiComune = (props) => {
    const [validated, setValidated] = useState(false)
    const [isLoading, setIsLoading] = useState(false)
    const [success, setSuccess] = useState({success: false, message: ""})
    const [comuneTemp, setComuneTemp] = useState({
            nome: "",
            acronimo: "",
            cod_provincia: "",
            area_territoriale_id: "",
            cliente_id: ""
      })

    const handleSubmit = async (e) =>{
        e.preventDefault()
        const form = e.currentTarget
        if (form.checkValidity() === false) {
        }
        setValidated(true)
        setIsLoading(true)
        try{
            const res = await axiosPrivate.post("/comune", comuneTemp)
            setSuccess({success: true, message: res.data.message})
            setIsLoading(false)
        }
        catch (err){
            console.log(err)
            setIsLoading(false)
        }
    }

    const formData = [
        [
          {
            required: true,
            id: "nome",
            label: "Nome",
            type : "input",
            area_territoriale_id: "",
            cliente_id: ""
          },
          {
            required: true,
            id: "acronimo",
            label: "acronimo",
            type : "input",
            placeholder: ""
          },
          {
            required: true,
            id: "cod_provincia",
            label: "Codice provincia",
            type : "input",
            placeholder: ""
          },
        ],
        [
          {
            required: true,
            id: "area_territoriale_id",
            label: "Area Territoriale",
            type : "select",
            options: props.territori
          },
          {
            required: true,
            id: "cliente_id",
            label: "Cliente associato",
            type : "select",
            options: props.clienti
          },
        ]
      ]

    return (
        <>
            { isLoading && <Spinner/>}
            <CModal size="xl" visible={props.isVisible} onClose={() => props.onClose()}>
            <CModalHeader style={{backgroundColor: "#E5F4FF"}}>
                <CModalTitle>{props.title}</CModalTitle>
            </CModalHeader>
            <CModalBody>
                {success.success &&  <CCallout className="py-2" color="success">
                    <h1 className="size-header m-0">Successo</h1>
                    <p className="size-text m-0">{success.message}</p>
                </CCallout>}
                <CForm onSubmit={handleSubmit} noValidate validated={validated}>
                    <BaseForm state={comuneTemp} setStateFunction={setComuneTemp} data_set={formData} />
                    <CButton type='submit' shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm my-2">Crea</CButton> 
                </CForm>
            </CModalBody>
            </CModal>
        </>
    )
}