import { useState } from 'react'
import { CCol, CRow, CFormInput, CButton } from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilMinus, cilPlus } from '@coreui/icons'
import { useOutletContext } from 'react-router-dom'

export const Note = (props) => {
    const [plData, setPlData] = useOutletContext()
    const [tempNote, setTempNote] = useState("")
    return (
            <CRow>
            <CCol>
                <div className='size-subHeader color-base weight-700'>Note</div>
                {plData.note.map((nota, index) =>
                    <div className='d-flex my-2'>
                        <CFormInput
                            placeholder={nota}
                            className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                            onChange={(e) => setPlData({...plData, note: plData.note.map((oldNota, oldIndex) => {
                                if(oldIndex === index) return e.target.value
                                return oldNota
                            })})}
                        />
                        <CButton onClick={() => setPlData({...plData, note: plData.note.filter((oldNote, nodeIndex) => nodeIndex !== index)})} shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm mx-2 weight-600 px-2"><CIcon size='sm' icon={cilMinus}/></CButton>
                    </div>
                    )}
                <div className='d-flex my-2'>
                <CFormInput
                placeholder={`Nota #${plData.note.length + 1}`}
                className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                onChange={e => setTempNote(e.target.value)}
                />
                <CButton onClick={() => setPlData({...plData, note : [...plData.note, tempNote]})} shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm mx-2 weight-600 px-2"><CIcon size='sm' icon={cilPlus}/></CButton>
                </div>
            </CCol>
            <CCol>
                {
                    <ul className='mt-1'>
                    {plData.note.map(nota =>
                        <li className='size-text'>{nota}</li>
                        )}
                    </ul>
                }
            </CCol>
        </CRow>
    )
}
