import React from 'react'
import BaseForm from '../../../Forms/baseForm'
import { useOutletContext } from 'react-router-dom'
export const DatiGeneraliPl = (props) => {
    const [plData, setPlData] = useOutletContext()
    const formData = [
        [
            {
                required: false,
                id: "committente",
                label: "Committente",
                type : "select",
                options : ["CITEL", "CITELUM"]
            },
            {
                required: false,
                id: "costruttore",
                label: "Costruttore",
                type : "select",
                options : ["SIDERPALI"]
            },
            {
                required: false,
                id: "data",
                label: "Data",
                type : "date"
            }
        ],
        [
            {
                required: false,
                id: "tbd",
                label: "TBD",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "commessa",
                label : "Commessa",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "tipologia",
                label: "Tipologia",
                type : "select",
                options : ["Torre a sezione poligonale..."]
            }
        ],
        [
            {
                required: false,
                id: "n_torre",
                label: "N. Torre",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "tipoligia_tirafondi",
                label : "Tipologia Tirafondi",
                type : "select",
                options : []
            },
            {
                required: false,
                id: "dist_asse_tirafondi",
                label: "Dist. Asse Tirafondi- Fusto Torre (cm)",
                type : "input",
                placeholder : "Torre a sezione poligonale..."
            }
        ],
        [
            {
                required: false,
                id: "n_tirafondi",
                label: "N. Tirafondi",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "diametro_base",
                label : "Diametro alla base",
                type : "input",
                placeholder : ""
            },
            {
                required: false,
                id: "altezza",
                label: "Altezza Torre (m)",
                type : "input",
                placeholder : ""
            }
        ],
        [
            {
                required: false,
                id: "materiale",
                label: "Materiale",
                type : "select",
                options : []
            },
            {
                required: false,
                id: "cond_superficiali",
                label : "Condizioni Superficiali",
                type : "select",
                options : []
            },
            {
                required: false,
                id: "planimetria",
                label: "Carica Planimetria Ubicazione torre",
                type : "file",
                placeholder : "No file chosen"
            }
        ],
        [
            {
                required: false,
                id: "indirizzo",
                label: "Cerca su Google Maps",
                type : "input",
                placeholder : "Maps"
            }
        ]
        ]
    return (
        <BaseForm state={plData} setStateFunction={setPlData} data_set={formData}/>
    )
}
