import { CModal, CModalHeader, CModalBody, CModalTitle, CForm, CButton } from '@coreui/react'
import { useEffect, useState } from 'react'
import useAuth from '../../hooks/useAuth'
import BaseForm from '../baseForm'
import Spinner from '../../Components/spinner'
import { axiosPrivate } from '../../api/axios'
export const ModalNuovaCommessa = (props) => {
    const [validated, setValidated] = useState(false)
    const [isLoading, setIsLoading] = useState(false)
    const [comuni, setComuni] = useState([])
    const [clienti, setClienti] = useState([])
    const [referenti, setReferenti] = useState([])
    const [fornitori, setFornitori] = useState([])
    const [configurazione, setConfigurazione] = useState([])
    const {auth} = useAuth()
    const onClose = () =>{
        props.onClose()
    }

    const [commessaData, setCommessaData] = useState({
        user_id: auth.id, 
        modificato_da: auth.id,
        data_modifca: new Date(),
        comune : "",
        cliente : "",
        referente : "",
        fornitore_1 : "",
        fornitore_2 : "",
        data_inizio : "",
        data_scadenza : "",
        configurazione : "",
        azienda_manutentrice : "",
        contatti_manutentore : "",
        codice_originario : "",
        tipo_verifica : "",
        note : "",
        note_interne : "",
        pl: []
        })
    const formData = [
        [
            {
                required: true,
                id: "comune",
                label: "Comune",
                type : "select",
                options : comuni
            },
            {
                required: true,
                id: "cliente",
                label: "cliente",
                type : "select",
                options : clienti
            },
            {
                id: "referente",
                label: "Referente cliente",
                type : "select",
                options : referenti
            },
        ],
        [
            {
                required: true,
                id: "fornitore_1",
                label: "Fornitore",
                type : "select",
                options : fornitori
            },
            {
                id: "fornitore_2",
                label: "2 Fornitore",
                type : "select",
                options : fornitori
            },
            {
                required: true,
                id: "data_inizio",
                label: "Data di inizio",
                type : "date"
            }
        ],
        [
            {
                required: true,
                id: "data_scadenza",
                label: "Data di scadenza",
                type : "date"
            },
            {
                required: true,
                id: "configurazione",
                label : "Configurazione",
                type : "select",
                options : configurazione
            },
            {
                id: "azienda_manutentrice",
                label: "Azienda manutentrice",
                type : "input",
                placeholder : ""
            }
        ],
        [
            {
                id: "contatti_manutentore",
                label: "Contatti manutentore",
                type : "input",
                placeholder : ""
            },
            {
                id: "codice_originario",
                label: "Codice commessa originaria",
                type : "input",
                placeholder : ""
            },
            {
                id: "tipo_verifica",
                label : "Tipo verifica commessa",
                type : "select",
                options : ["Pali", "Torrifaro"]
            }
        ],
        [
            {
                id: "note",
                label : "Note",
                type : "input",
                placeholder : ""
            }
        ],
        [
            {
                id: "note_interne",
                label : "Note interne",
                type : "input",
                placeholder : ""
            }
        ]
            ]

    const handleSubmit = async (e) =>{
        e.preventDefault()
        const form = e.currentTarget
        if (form.checkValidity() === false) {
            setValidated(true)
            return 0
        }
        setValidated(true)
        setIsLoading(true)
        try{
            const res = await axiosPrivate.post("/commessa/createCommessa", commessaData)
            console.log(res.data)
            setIsLoading(false)
        }
        catch (err){
            console.log(err)
            setIsLoading(false)
        }
    }

    useEffect(() =>{
        const getComuni = async () =>{
            setIsLoading(true)
            try{
                const res = await axiosPrivate.get("comuni")
                const data = []
                res.data.forEach(comune => {
                data.push({value: comune.comune_id, label: comune.nome})
                });
                setComuni(data)
                setIsLoading(false)
            }
            catch (err){
                setIsLoading(false)
            }
          }
        const getClienti = async () =>{
            setIsLoading(true)
            try{
                const res = await axiosPrivate.get("clienti")
                const data = []
                res.data.forEach(cliente => {
                data.push({value: cliente.cliente_id, label: cliente.ragione_sociale})
                });
                setClienti(data)
                setIsLoading(false)
            }
            catch (err){
                setIsLoading(false)
            }
          }
        const getFornitori = async () =>{
            setIsLoading(true)
            try{
                const res = await axiosPrivate.get("fornitori")
                const data = []
                res.data.forEach(cliente => {
                data.push({value: cliente.cliente_id, label: cliente.ragione_sociale})
                });
                setFornitori(data)
                setIsLoading(false)
            }
            catch (err){
                setIsLoading(false)
            }
          }
        getComuni()
        getClienti()
        getFornitori()
    }, [])

    return (
        <>
            {isLoading && <Spinner/>}
            <CModal size="xl" visible={props.isVisible} onClose={() => onClose()}>
            <CModalHeader style={{backgroundColor: "#E5F4FF"}}>
                <CModalTitle>{props.title}</CModalTitle>
            </CModalHeader>
            <CModalBody>
                <CForm onSubmit={handleSubmit}
                noValidate
                validated={validated}>
                    <BaseForm state={commessaData} setStateFunction={setCommessaData} data_set={formData} />
                    <CButton type='submit' shape="rounded-1 primary border-0 size-text d-flex align-items-center shadow-sm my-2">Invia</CButton> 
                </CForm>
            </CModalBody>
            </CModal>
        </>
    )
}
