import { CFormInput, CFormSelect, CFormCheck, CCol, CRow } from "@coreui/react"
import { CDatePicker } from "@coreui/react-pro"

function BaseForm (props){
    const state = props.state
    const setStateFunction = props.setStateFunction
    const data_set = props.data_set
    return (
        data_set.map((row, index) =>
            <CRow key={index} className='w-100 justify-content-center mt-1'>
                {row.map((col, index) =>
                    <CCol key={index}>
                        {col.type !== "check" && col.type !== "multi-check" && col.type !== "title" ? <div className='size-text color-base weight-600 mb-2'>{col.label}</div> : <></>}
                        {col.type === 'select'?
                            (<CFormSelect options={[{label: "Seleziona", value: "", disabled: true}].concat(col.options)} required={col.required} onChange={e => setStateFunction({...state, [col.id] : e.target.value})} aria-label="Default select example" className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'/>)
                            : col.type === 'input'?
                                (<CFormInput
                                    placeholder={col.placeholder}
                                    onChange={e => setStateFunction({...state, [col.id] : e.target.value})}
                                    value={state[col.id]}
                                    className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                                    required={col.required}
                                    readOnly={col.readOnly || false}
                                    />)
                            : col.type === 'title'?
                                (<div className='size-header color-base weight-700 mb-2'>{col.label}</div>)
                            : col.type === 'check'?
                            (
                                <CFormCheck 
                                value={col.label}
                                checked={state[col.id] === col.label}
                                onChange={e => setStateFunction({...state, [col.id] : e.target.value})}
                                className='size-subHeader color-base weight-600 mb-2 rounded-1' label={col.label}/>
                                )
                            : col.type === 'checkbox'?
                            (
                                <CFormCheck 
                                value={!state[col.id]}
                                checked={state[col.id]}
                                onChange={e => setStateFunction({...state, [col.id] : e.target.checked})}
                                className='size-subHeader color-base weight-600 mb-2 rounded-1' label={col.label}/>
                                )
                            : col.type === 'multi-check'?
                                (
                                col.checks.map((check, index) =>
                                    <CFormCheck className='size-text color-base weight-400 mb-2 mx-4 rounded-1' 
                                    key={check}
                                    value={check}
                                    checked={state[col.id].includes(check)}
                                    onChange={e => e.target.checked ? setStateFunction({...state, [col.id] : [...state[col.id], e.target.value]}) : setStateFunction({...state, [col.id] : state[col.id].filter(check => check !== e.target.value)})}
                                    label={check}/>
                                    )
                                )
                            : col.type === 'file'?
                                (<CFormInput
                                    type="file"
                                    required={col.required}
                                    onChange={e => setStateFunction({...state, [col.id] : e.target.files[0]})}
                                    className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
                                    />)
                            : col.type === 'date'?
                                (<CDatePicker
                                    className='w-75'
                                    locale='It-it'
                                    size="sm"
                                    value={state[col.id]}
                                    footer
                                    required={col.required}
                                    onDateChange={date => setStateFunction({...state, [col.id] : new Date(date)})}
                                    />)
                            : <div></div>
                        }
                    </CCol>
                )}
            </CRow>
        )
    )
}


export default BaseForm